.user-number, .user-location{
  background: transparent;
  border: 0 solid;
  border-bottom: 1px solid rgba(white, .5);
  color: white;
  display: block;
  margin: 1rem;
  padding: .5rem;
  -webkit-transition: 250ms background ease-in;
  transition: 250ms background ease-in;
  width: calc(100% - 3rem);
&:focus {
    background: #666666;
    color: black;
    -webkit-transition: 250ms background ease-in;
    transition: 250ms background ease-in;
}
}
.user-submit {
  border: 1px solid white;
  background: transparent;
  color: white;
  display: block;
  margin: 1rem auto;
  min-width: 1px;
  padding: .25rem;
  -webkit-transition: 250ms background ease-in;
  transition: 250ms background ease-in;
&:hover, &:focus {
    background: white;
    color: black;
    -webkit-transition: 250ms background ease-in;
    transition: 250ms background ease-in;
}
}
#button {
  display: block;
  padding: .8rem;
  font-size: 74%;
  bottom: 0;
  left:47%;
