  text-align: center;
  text-decoration: none;
  outline: none;
  color: white;
  background-color: #666666;
  border: none;
  position: absolute;
  border-radius: 2px;
  opacity: 0.6;
}
#button:hover {background-color: #3c3c3c
}
#button:active {
  background-color: #666666;
  -webkit-box-shadow: 0 5px #666;
          box-shadow: 0 5px #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}














































































































